@import url("https://fonts.googleapis.com/css2?family=General+Sans:wght@400;500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --heading-h4: "General Sans";

  /* font sizes */
  --font-size-xl: 1.25rem;
  --font-size-13xl: 2rem;
  --font-size-29xl: 3rem;
  --heading-h4-size: 1.13rem;
  --font-size-21xl: 2.5rem;

  /* Colors */
  --colour-pallete-neutrals-n20: #f5f5f5;
  --colour-pallete-neutrals-n700: #1c1c1c;
  --colour-pallete-primary-p100: #7fd2f1;
  --colour-pallete-primary-p300: #22b2e6;
  --colour-pallete-neutrals-n0: #fff;
  --colour-pallete-neutrals-n900: #000;

  /* Gaps */
  --gap-xl: 1.25rem;
  --gap-9xs: 0.25rem;
  --gap-base: 1rem;
  --gap-5xl: 1.5rem;
  --gap-13xl: 2rem;
  --gap-xs: 0.75rem;
  --gap-3xs: 0.63rem;

  /* Paddings */
  --padding-5xs: 0.5rem;
  --padding-base: 1rem;

  /* border radiuses */
  --br-8xs: 5px;
  --br-3xs: 10px;
  --br-131xl: 150px;
}
